.content {
    width: 100vw;
    height: 20vh;
  
    display: flex;
  
    align-items: center;
  
    justify-content: center;
  }
  
  .slider-wrapper {
    font-size: 45px;
  
    color: black;
  
    font-weight: 12px;
  
    text-transform: uppercase;
  
    display: flex;
  
    align-items: center;
  
    justify-content: center;
  }
  
  .slider {
    height: 50px;
  
    font-size: 26px;
  
    padding-left: 15px;
  
    overflow: hidden;
  }
  
  .slider div {
    color: white;
    font-weight: bold;
  
    height: 50px;
  
    margin-bottom: 50px;
  
    padding: 2px 15px;
  
    text-align: center;
  
    box-sizing: border-box;
  }
  
  .slider-text1 {
    background: rgb(183, 140, 240);
  
    animation: slide 4s linear infinite;
  }
  
  .slider-text2 {
    background: rgb(223, 160, 45);
  }
  
  .slider-text3 {
    background: lightcoral;
  }
  
  @keyframes slide {
    0% {
      margin-top: -400px;
    }
  
    5% {
      margin-top: -200px;
    }
  
    33% {
      margin-top: -200px;
    }
  
    38% {
      margin-top: -100px;
    }
  
    66% {
      margin-top: -100px;
    }
  
    71% {
      margin-top: 0px;
    }
  
    100% {
      margin-top: 0px;
    }
  }
  @import url(https://fonts.googleapis.com/css?family=Open + Sans);
  
  @mixin stagger-anim-delay($i, $initial-delay, $stagger-delay) {
    @while $i > 0 {
      &:nth-of-type(#{$i}) {
        animation-delay: $initial-delay + $stagger-delay * $i;
      }
      $i: $i - 1;
    }
  }
  
  body {
    margin-top: 20px;
  
    color: #282828;
  }
  
  .skills {
    width: 80%;
    max-width: 960px;
    height: 920px;
    margin-bottom: 20px;
    margin-left: 40px;
  
    position: relative;
  }
  
  .lines {
    height: 100%;
    position: relative;
    list-style-type: none;
  }
  .line {
    height: inherit;
    width: 2px;
    list-style-type: none;
  
    position: absolute;
  
    background: rgba(rgb(84, 27, 160), 0.6);
  }
  .l--0 {
    left: 0;
  }
  .l--25 {
    left: 14%;
  }
  .l--50 {
    left: 40%;
  }
  .l--75 {
    left: 65%;
  }
  .l--100 {
    left: calc(90% - 6px);
  }
  
  .line__label {
    display: block;
    width: 100px;
    text-align: center;
  
    position: absolute;
    bottom: -20px;
    right: -50px;
  }
  .title {
    text-transform: uppercase;
    font-weight: bold;
  }
  
  .charts {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
  }
  
  .chart {
    margin: 30px 0 0;
  }
  .first-child {
    margin: 0;
  }
  
  .chart__title {
    display: block;
    margin: 0 0 10px;
  
    font-weight: bold;
  
    opacity: 0;
  
    animation: 2.5s anim-lightspeed-in ease forwards;
  }
  
  .chart--design {
    animation-delay: 4.5s;
  }
  
  .chart--horiz {
    overflow: hidden;
  }
  
  .chart__bar {
    list-style-type: none;
    border: 4px;
    height: 30px;
    margin-bottom: 10px;
  
    background: linear-gradient(to left, #4cb8c4, #3cd3ad);
    border-top-right-radius: border-rad;
    border-bottom-right-radius: border-rad;
    opacity: 0;
  
    animation: 1s anim-lightspeed-in ease forwards;
  }
  .chart--dev {
    @include stagger-anim-delay(11, 0.5s, 0.2s);
  }
  .chart--prod {
    @include stagger-anim-delay(2, 3.8s, 0.2s);
  }
  .chart--design {
    @include stagger-anim-delay(3, 5s, 0.2s);
  }
  
  .chart__label {
    padding-left: 10px;
    line-height: 30px;
    color: black;
  }
  .card {
    background-color: #ebeef0;
    color: black;
    padding: 1rem;
    /* height: 4rem; */
  }
  
  .cards {
    max-width: 1400px;
    margin: 0 auto;
    display: grid;
    padding: 0;
    /* border: 0px;
    border-style: "None"; */
    grid-template-rows: auto;
  
    /* grid-gap: 1rem; */
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
  ul.b {
    background: #3399ff;
  padding: 20px;
    list-style-type: square;
  }
  .x {
    background: #cce5ff;
    margin: 5px;
  }
  
  @keyframes anim-lightspeed-in {
    0% {
      transform: translateX(-200%);
      opacity: 1;
    }
    100% {
      transform: translateX(0);
      opacity: 1;
    }
  }