body {
  margin: 0;
  padding: 0;
  font-size: 100%;
  line-height: 1.6;
  font-family: Arial, Helvetica, sans-serif;
}

#wrapper {
  position: relative;
  width: 100%;
  min-height: 100vw;
  overflow: hidden;
}

.layer {
  position: absolute;
  width: 100vw;
  min-height: 100vw;
  overflow: hidden;
}

.layer .content-wrap {
  position: absolute;
  width: 100vw;
  min-height: 55vw;
}

.layer .content-body {
  width: 25%;
  position: absolute;
  top: 50%;
  text-align: center;
  transform: translateY(-50%);
  color: #fff;
}

.layer img {
  position: absolute;
  width: 45%;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.layer h1 {
  font-size: 2em;
}

.bottom {
  background: #222;
  z-index: 1;
}

.bottom .content-body {
  right: 2%;
}

.bottom h1 {
  color: teal;
}

.top {
  background: #eee;
  color: #222;
  z-index: 2;
  width: 50vw;
}

.top .content-body {
  left: 2%;
  color: #222;
}

.handle {
  position: absolute;
  height: 100%;
  display: block;
  background-color: teal;
  width: 5px;
  top: 0;
  left: 50%;
  z-index: 3;
}

.skewed .handle {
  top: 50%;
  transform: rotate(10deg) translateY(-50%);
  height: 200%;
  transform-origin: top;
}

.skewed .top {
  transform: skew(-10deg);
  margin-left: -1000px;
  width: calc(50vw + 1000px);
}

.skewed .top .content-wrap {
  transform: skew(10deg);
  margin-left: 1000px;
}

@media (max-width: 768px) {
  body {
    font-size: 75%;
  }
}
.content {
    width: 100vw;
    height: 20vh;
  
    display: flex;
  
    align-items: center;
  
    justify-content: center;
  }
  
  .slider-wrapper {
    font-size: 45px;
  
    color: black;
  
    font-weight: 12px;
  
    text-transform: uppercase;
  
    display: flex;
  
    align-items: center;
  
    justify-content: center;
  }
  
  .slider {
    height: 50px;
  
    font-size: 26px;
  
    padding-left: 15px;
  
    overflow: hidden;
  }
  
  .slider div {
    color: white;
    font-weight: bold;
  
    height: 50px;
  
    margin-bottom: 50px;
  
    padding: 2px 15px;
  
    text-align: center;
  
    box-sizing: border-box;
  }
  
  .slider-text1 {
    background: rgb(183, 140, 240);
  
    -webkit-animation: slide 4s linear infinite;
  
            animation: slide 4s linear infinite;
  }
  
  .slider-text2 {
    background: rgb(223, 160, 45);
  }
  
  .slider-text3 {
    background: lightcoral;
  }
  
  @-webkit-keyframes slide {
    0% {
      margin-top: -400px;
    }
  
    5% {
      margin-top: -200px;
    }
  
    33% {
      margin-top: -200px;
    }
  
    38% {
      margin-top: -100px;
    }
  
    66% {
      margin-top: -100px;
    }
  
    71% {
      margin-top: 0px;
    }
  
    100% {
      margin-top: 0px;
    }
  }
  
  @keyframes slide {
    0% {
      margin-top: -400px;
    }
  
    5% {
      margin-top: -200px;
    }
  
    33% {
      margin-top: -200px;
    }
  
    38% {
      margin-top: -100px;
    }
  
    66% {
      margin-top: -100px;
    }
  
    71% {
      margin-top: 0px;
    }
  
    100% {
      margin-top: 0px;
    }
  }
  
  @mixin stagger-anim-delay($i, $initial-delay, $stagger-delay) {
    @while $i > 0 {
      &:nth-of-type(#{$i}) {
        -webkit-animation-delay: $0s-delay + $stagger-delay * $i;
                animation-delay: $0s-delay + $stagger-delay * $i;
        -webkit-animation-delay: $initial-delay + $stagger-delay * $i;
                animation-delay: $initial-delay + $stagger-delay * $i;
      }
      $i: $i - 1;
    }
  }
  
