    background: rgba(rgb(84, 27, 160), 0.6);
  }
  .l--0 {
    left: 0;
  }
  .l--25 {
    left: 14%;
  }
  .l--50 {
    left: 40%;
  }
  .l--75 {
    left: 65%;
  }
  .l--100 {
    left: calc(90% - 6px);
  }
  
  .line__label {
    display: block;
    width: 100px;
    text-align: center;
  
    position: absolute;
    bottom: -20px;
    right: -50px;
  }
  .title {
    text-transform: uppercase;
    font-weight: bold;
  }
  
  .charts {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
  }
  
  .chart {
    margin: 30px 0 0;
  }
  .first-child {
    margin: 0;
  }
  
  .chart__title {
    display: block;
    margin: 0 0 10px;
  
    font-weight: bold;
  
    opacity: 0;
  
    -webkit-animation: 2.5s anim-lightspeed-in ease forwards;
  
            animation: 2.5s anim-lightspeed-in ease forwards;
  }
  
  .chart--design {
    -webkit-animation-delay: 4.5s;
            animation-delay: 4.5s;
  }
  
  .chart--horiz {
    overflow: hidden;
  }
  
  .chart__bar {
    list-style-type: none;
    border: 4px;
    height: 30px;
    margin-bottom: 10px;
  
    background: linear-gradient(to left, #4cb8c4, #3cd3ad);
    border-top-right-radius: border-rad;
    border-bottom-right-radius: border-rad;
    opacity: 0;
  
    -webkit-animation: 1s anim-lightspeed-in ease forwards;
  
            animation: 1s anim-lightspeed-in ease forwards;
  }
  .chart--dev {
    @include stagger-anim-delay(11, 0.5s, 0.2s);
  }
  .chart--prod {
    @include stagger-anim-delay(2, 3.8s, 0.2s);
  }
  .chart--design {
    @include stagger-anim-delay(3, 5s, 0.2s);
  }
  
  .chart__label {
    padding-left: 10px;
    line-height: 30px;
    color: black;
  }
