body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.g-card {
  margin: 20px;
}


.g-card-image {
  border-radius: 10px;
  width: 300px;
  height: 300px;
  box-shadow: 0px 0px 3px 1px #ccc;
}


.g-card-info {
  margin-top: 10px;
  min-height: 100px;
}


.g-card-title {
  font-size: 24px;
  margin: 0px;
}


.g-card-sub-title {
  font-size: 16px;
  margin: 0px;
}


.success-msg {
  color: #57A773;
  padding: 10px 15px;
}

.wrapper {
  /* display: flex; */
  display:inline-flex;
  width: 400px;
  margin-left: 100px;
  margin-bottom: 100px;
  margin-right: 100px;
  align-items: center;
  /* position: absolute; */
}
.counter {
  flex-wrap: wrap;
  display: flex;
  align-items: center;
  background-color: #09538f;
  padding: 10px 10px 10px 10px;
  border-radius: 5px;
}
.count-title {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  font-size: 60px;
  margin-left: 20px;
  color: white;
  font-weight: normal;
  margin-bottom: 0;
  margin-right: 10px;
  text-align: center;
}
.count-text {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  font-size: 20px;
  color: yellow;
  font-weight: normal;
  margin-top: 10px;
  margin-bottom: 0;
  margin-right: 10px;
  text-align: center;
}
.f2 {
  margin: 0 auto;
  float: none;
  display: table;
  color: white;
}
.col_fourth {
  flex: 1;
  /* width: 223.5%; */
  position: relative;
  /* display: inline; */
  /* display: inline-block; */
  float: left;
  margin-right: 2%;
  margin-top: 100px;
  /* margin-bottom: 100px; */
}

.Form {
  background-color: black;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  opacity: 50%;
}

.Form > .btn {
  opacity: 1;
}
